*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
}

.page {
  font-family: "Poppins", sans-serif;
  box-sizing: border-box;

  scroll-behavior: smooth;

  &__body {
    background: #eee;
    margin: 0;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background-color: #2c2c2c;

    transform: translateX(-100%);
    transition: transform 0.3s, opacity 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 64px;

    @include onDesktop {
      margin-bottom: 104px;
    }
  }

  &__section--title {
    margin: 0 0 32px;

    font-weight: bold;
    font-size: 36px;
    line-height: 36px;
    text-align: center;
    letter-spacing: -1.5px;
    color: #000;

    @include onDesktop {
      margin-bottom: 56px;
      font-size: 48px;
      line-height: 48px;
    }
  }
}
